import React, { Component } from "react";
import axios from "axios";
import "./scss/main.scss";
import Search from "./components/Search";
import Pagination from "./components/Pagination";
import Results from "./components/Results";

const statics = {
  apiKey:
    "apikey=2dbf364b46d22dbe140f169f78769f82&ts=1&hash=8b1947aa2e144419719d0f1756d03f2a",
  limit: "limit=20",
  comicFilter:
    "&format=comic&formatType=comic&noVariants=true&hasDigitalIssue=true&orderBy=onsaleDate"
};

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      init: false,
      type: "characters",
      input: "",
      searchInput: "",
      searchValue: "",
      data: [],
      error: null,
      lastEnd: "",
      footer:
        '<a href="http://marvel.com">Data provided by Marvel.  2018 MARVEL</a>'
    };

    this.handleTypeChange = this.handleTypeChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.turnCard = this.turnCard.bind(this);
  }

  handleTypeChange(event) {
    this.setState({ type: event.target.value });
  }

  handleInputChange(event) {
    this.setState({ input: event.target.value });
  }

  handlePageChange(e) {
    const offset = (e.target.value - 1) * 20;
    const end = `${this.state.lastEnd}&offset=${offset}`;
    console.log(end);
    this.fetchData(end);
  }

  handleClick(e) {
    const end = `${e.target.dataset.end.replace("http", "https")}?${
      statics.apiKey
    }${e.target.dataset.type === "comics" ? statics.comicFilter : ""}`;
    this.setState({
      searchValue: e.target.dataset.type,
      lastEnd: end,
      loading: true
    });
    this.fetchData(end);
  }

  handleSubmit(e) {
    e.preventDefault();
    document
      .querySelectorAll(".search-form input")
      .forEach(item => item.blur());
    const end = `https://gateway.marvel.com/v1/public/${this.state.type}?${
      this.state.type === "series" ? "title" : "name"
    }StartsWith=${this.state.input}&${statics.apiKey}`;
    this.setState({
      searchInput: this.state.input,
      searchValue: this.state.type,
      lastEnd: end,
      loading: true,
      init: true
    });
    this.fetchData(end);
  }

  fetchData(end) {
    axios
      .get(end)
      .then(response => response.data)
      .then(myJson => {
        this.setState({
          loading: false,
          data: myJson.data.results,
          pages: Math.ceil(myJson.data.total / 20),
          footer: myJson.attributionHTML
        });
      })
      .catch(error => {
        console.dir(error);
        this.setState({
          loading: false,
          error
        });
      });
  }

  turnCard(e) {
    e.currentTarget.classList.toggle("turn");
  }

  render() {
    const { loading, data, error, searchValue: type, init } = this.state;
    const results = {
      type: type,
      onClick: this.handleClick,
      data: data,
      turnCard: this.turnCard,
      init: init
    };
    const search = {
      type: type,
      onSubmit: this.handleSubmit,
      typeChange: this.handleTypeChange,
      inputChange: this.handleInputChange
    };
    function createMarkup(text) {
      return { __html: text };
    }

    return (
      <div className="App">
        <Search {...search} />
        {error && <h1>{error.message}</h1>}
        {this.state.pages > 1 && (
          <Pagination
            pages={this.state.pages}
            changePage={this.handlePageChange}
          />
        )}
        {!loading ? (
          init ? (
            <Results {...results} />
          ) : (
            <div className="empty" />
          )
        ) : (
          <span>Loading...</span>
        )}

        <footer>
          <div className="author">
            <span>Designed and Coded by - </span>
            <a
              target="_blank"
              href="https://minggas.website"
              title="Link to Minggas Website"
              rel="noopener noreferrer"
            >
              Minggas
            </a>
          </div>
          <div dangerouslySetInnerHTML={createMarkup(this.state.footer)} />
        </footer>
      </div>
    );
  }
}

export default App;

